import React from 'react';
import type { Action, ConvertToPluginActionCreators, ConvertToPluginActionPredicates } from '@ali/4ever-cangjie';
import type { DraggableDataProps, DragHanderProps, DropHolderProps, DragMenuAction } from '../types';

/** ***************** for UI ***************** */

export const CLEAR_DROPHOLDER = Symbol('CLEAR_DROPHOLDER');
export const UPDATE_DRAGHANDER = Symbol('UPDATE_DRAGHANDER');
export const UPDATE_DROPHOLDER = Symbol('UPDATE_DROPHOLDER');

/** ***************** for controller ***************** */
export const DELETE_DARGFRAGMENT = Symbol('DELETE_DARGFRAGMENT');
export const UPDATE_DARGFRAGMENT = Symbol('UPDATE_DARGFRAGMENT');
export const UPDATE_DRAGHIGHLIGHT = Symbol('UPDATE_DRAGHIGHLIGHT');		
export const REMOVE_DRAGHIGHLIGHT_SINGLE = Symbol('REMOVE_DRAGHIGHLIGHT_SINGLE');
export const DROP_FRAGMENT = 'DROP_FRAGMENT';
export const DROP_EVENT_FRAGMENT = Symbol('DROP_EVENT_FRAGMENT');
export const DROP_HTML = Symbol('DROP_HTML');
export const COPY_AS_URL = Symbol('COPY_AS_URL');
export const UPDATE_IMAGE_DROP_HOLDER = Symbol('UPDATE_IMAGE_DROP_HOLDER');

export const DRAG_MENU_INSERT_BEFORE = Symbol('DRAG_MENU_INSERT_BEFORE');
export const DRAG_MENU_INSERT_AFTER = Symbol('DRAG_MENU_INSERT_AFTER');
export const DRAG_MENU_CUT_OR_COPY = Symbol('DRAG_MENU_CUT_OR_COPY');
export const DRAG_MENU_DELETE = Symbol('DRAG_MENU_DELETE');
export const MOUNT_LINE_HEADER_TOOLBAR = Symbol('MOUNT_LINE_HEADER_TOOLBAR');

interface DropFragmentAction {
  type: typeof DROP_FRAGMENT;
  payload?: DraggableDataProps
}

export function dropFragment(payload?: DraggableDataProps): DropFragmentAction {
  return {
    type: DROP_FRAGMENT,
    payload,
  };
}

function isDropFragmentAction(action: Action): action is DropFragmentAction {
  return action.type === DROP_FRAGMENT;
}

export function dropEventFragment(payload: DraggableDataProps) {
  return {
    type: DROP_EVENT_FRAGMENT,
    payload,
  };
}

export function dropHtml(payload) {
  return {
    type: DROP_HTML,
    payload,
  };
}

export function clearDropHolder() {
  return {
    type: CLEAR_DROPHOLDER,
  };
}

export function updateDragHander(payload?: DragHanderProps) {
  return {
    type: UPDATE_DRAGHANDER,
    payload,
  };
}

export function deleteDragFragment(payload) {
  return {
    type: DELETE_DARGFRAGMENT,
    payload,
  };
}

export function updateDragFragment(payload?: any) {
  return {
    type: UPDATE_DARGFRAGMENT,
    payload,
  };
}

export function updateDragHighlight(payload) {		
  return {		
    type: UPDATE_DRAGHIGHLIGHT,		
    payload,		
  };		
}		
export function removeDragHighlightSingle(payload) {		
  return {		
    type: REMOVE_DRAGHIGHLIGHT_SINGLE,		
    payload,		
  };		
}

export function updateDropHolder(payload) {
  return {
    type: UPDATE_DROPHOLDER,
    payload,
  };
}

export function copyAsUrl(url: string) {
  return {
    type: COPY_AS_URL,
    payload: {
      url,
    },
  };
}

export interface IUpdateImageDropHolderPayload {
  dropHolder?: DropHolderProps | null;
  event: DragEvent;
  dispatch: React.Dispatch<any>;
}

export function updateImageDropHolder(payload: IUpdateImageDropHolderPayload) {
  return {
    type: UPDATE_IMAGE_DROP_HOLDER,
    payload,
  };
}

export function dragMenuInsertBefore(payload: DragMenuAction) {
  return {
    type: DRAG_MENU_INSERT_BEFORE,
    payload,
  };
}

export function dragMenuInsertAfter(payload: DragMenuAction) {
  return {
    type: DRAG_MENU_INSERT_AFTER,
    payload,
  };
}

export function dragMenuCutOrCopy(payload: DragMenuAction) {
  return {
    type: DRAG_MENU_CUT_OR_COPY,
    payload,
  };
}

export function dragMenuDelete(payload: DragMenuAction) {
  return {
    type: DRAG_MENU_DELETE,
    payload,
  };
}

/**
 * mount 埋点
 */
export function mountDragHander() {
  return {
    type: MOUNT_LINE_HEADER_TOOLBAR,
  };
}


export const actions = {}

export const isActions = { isDropFragmentAction }


declare module '@ali/4ever-cangjie' {
  /** type combination for commands、queries、actions */
  interface PluginActionCreators extends ConvertToPluginActionCreators<typeof actions>{}
  interface PluginActionPredicates extends ConvertToPluginActionPredicates<typeof isActions>{}
}
